import { atom } from "recoil";

export const bandmenuState = atom({
    key: 'bandmenuState',
    default: [
      { label: ' ', id: 'notice', index:0 },
      { label: ' ', id: 'concertinfo', index:1 },
      { label: '', id: 'gallery', index:2 },
      { label: ' ', id: 'community', index:3 }
    ]
  });

export const adminmenuState = atom({
    key: 'adminmenuState',
    default: [
      { label: ' ', id: 'usermanagement' },
      { label: ' ', id: 'bandrequest' },
    ]
  });

export const inputValueState = atom({
    key: 'inputValueState',
    default: '',
})

export const requestValueState = atom({
    key: 'requestValueState',
    default:'',
})

export const bandExistState = atom({
    key: 'bandExistState',
    default:false,
})

export const userDataState = atom({
    key:'userDataState',
    default:{
        boardName:'',
        boardRequest:'',
    }
})

export const likedState = atom({
    key: 'likedState',
    default:0,
})

export const isLikedState = atom({
    key: 'isLikedState',
    default:false,
})

export const postRowState = atom({
  key: 'postRowState',
  default: [
    { label: '', id: 'title' },
    { label: '', id: 'writer' },
    { label: ' ', id: 'createDate' },
    { label: '', id: 'view' },
    { label: '', id: 'like' },
    { label: '', id: 'userId' },
    { label: '', id: 'nickname' },
    { label: ' ', id: 'joinDate' },
    { label: '', id: 'role' },
    { label: '', id: 'id' },
    { label: '', id: 'contents' },
    { label: '', id: 'requestBoard' },
    { label: ' ', id: 'requestDate' },
  ]
});
