import React, { useState } from "react";
import styled from "styled-components";
import { Button, Input, Title } from "../../styled/ProjectStyle";
import axios from "axios";

const FindAccountBox = () => {
    const [email, setEmail] = useState('');
    const emailSubmitEvent = () => {
        axios.post("https://www.narock.site/account/email", {
            "emailValue": email,
        },
        // {withCredentials: true}
        )
        .then(function (response) {
            if (response.data.success) {
                console.log(response)
                alert("  .")
            }else{

                console.log(response)
            }      
        }).catch(function (error) {
            console.log(error);
            // alert(" ")
        })
    }
    return(
        <div>
            <Title> /  </Title>
            <Box>
                <div>
                    <SubTitle>   </SubTitle>
                    <Notice> /        .<br/>
                              ""    .
                    </Notice>
                    <Email>
                        <div>
                            <EmailLabel> </EmailLabel>
                            <Input onChange={e=>setEmail(e.target.value)}/>
                        </div>
                        <ButtonBox>
                            <Button value="" width="100%" height="34px" color="#3185FC" backgroundcolor="white" border="1px solid #3185FC" borderradius="5px"/>
                            <Button value="" width="100%" height="34px" borderradius="5px" onClick={emailSubmitEvent}/>
                        </ButtonBox>
                    </Email>
                </div>
            </Box>
        </div>
    )
}

const Box = styled.div`
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
`

const SubTitle = styled.div`
    color: #3185FC;
    font-size: 20px;
    font-weight:bold;
    margin-top:118px;
    margin-bottom:68px;
`

const Notice = styled.div`
    color: #3185FC;
    font-size: 20px;
    margin-bottom:57px;
`

const Email = styled.div`
    margin:0 auto;
    width:260px;
    box-sizing:border-box;
`
const EmailLabel = styled.div`
    text-align:left;
`

const ButtonBox = styled.div`
    display:flex;
    margin-bottom:100px;
`
export default FindAccountBox;